import main from  '../view/frame/main'

// 
const register =()=>  import ('../view/register/register')

// 
const clinical =()=>  import ('../view/clinical/clinical_index')

// 
const acupuncture =()=>  import ('../view/acupuncture/acupuncture_index')
const zj_zhijie =()=>  import ('../view/acupuncture/zj_zhijie')
const zj_select_symptom =()=>  import ('../view/acupuncture/zj_select_symptom')
const zj_select_treatment =()=>  import ('../view/acupuncture/zj_select_treatment')
const zj_discriminate =()=>  import ('../view/acupuncture/zj_discriminate')
const zj_symptom =()=>  import ('../view/acupuncture/zj_symptom')

// 
const anorectal =()=>  import ('../view/anorectal/anorectal_index')

// 
const patient_list =()=>  import ('../view/common/patient_list')
const treatment =()=>  import ('../view/common/treatment')
const immediate =()=>  import ('../view/common/zhijie')
const discriminate =()=>  import ('../view/common/bianzhengPlus')
const symptom =()=>  import ('../view/common/zhengxing')
const select_symptom =()=>  import ('../view/common/select_symptom')
const select_treatment =()=>  import ('../view/common/select_treatment')

// 
const database =()=>  import ('../view/system/database/database')
const db_xiyao =()=>  import ('../view/system/database/xiyao')
const db_zhongyao =()=>  import ('../view/system/database/zhongyao')
const db_danwei =()=>  import ('../view/system/database/danwei')
const db_muban =()=>  import ('../view/system/database/muban')
const db_xiangmu =()=>  import ('../view/system/database/xiangmu')
const db_huizong =()=>  import ('../view/system/database/huizong')
const db_mingxi =()=>  import ('../view/system/database/mingxi')
const db_kucun =()=>  import ('../view/system/database/kucun')

// 
const bb_jiuzhen =()=>  import ('../view/reports/jiuzhen')
const bb_ysgz =()=>  import ('../view/reports/ysgz')
const bb_ksgz =()=>  import ('../view/reports/ksgz')

// 
const auth =()=>  import ('../view/auth')

//  finance
const finance =()=>  import ('../view/finance')
// 
const drug =()=>  import ('../view/drug')

// 
const user_load =()=>  import ('../view/public/userInfoLoading')
const case_info =()=>  import ('../view/common/case_info')
const printPage =()=>  import ('../view/public/printPage')
const printPrescription =()=>  import ('../view/public/printPrescription')
const not_find =()=>  import ('../view/public/404')
const login =()=>  import ('../view/public/login')
const sign =()=>  import ('../view/public/sign')

const list = [
  {
    path: '/',
    name: 'main',
    component: main,
    children:[
      {
        name: '',
        path: '/register',
        component: register
      },
      {
        name: '',
        path: '/clinical',
        component: clinical,
        redirect: '/clinical/list',
        children:[
          {
            name: '-',
            path: '/clinical/list',
            component: patient_list
          },
          {
            name: '-',
            path: '/clinical/treatment',
            component: treatment
          },
          {
            name: '-',
            path: '/clinical/immediate',
            component: immediate
          },
          {
            name: '-',
            path: '/clinical/discriminate',
            component: discriminate
          },
          {
            name: '-',
            path: '/clinical/symptom',
            component: symptom
          },
          {
            name: '-',
            path: '/clinical/selectSymptom',
            component: select_symptom
          },
          {
            name: '-',
            path: '/clinical/selectTreatment',
            component: select_treatment
          },
          {
            name: '-',
            path: '/clinical/case_info',
            component: case_info
          },
        ]
      },
      {
        name: '',
        path: '/acupuncture',
        component: acupuncture,
        redirect: '/acupuncture/list',
        children: [
          {
            name: '-',
            path: '/acupuncture/list',
            component: patient_list
          },
          {
            name: '-',
            path: '/acupuncture/treatment',
            component: treatment
          },
          {
            name: '-',
            path: '/acupuncture/immediate',
            component: zj_zhijie
          },
          {
            name: '-',
            path: '/acupuncture/selectSymptom',
            component: zj_select_symptom
          },
          {
            name: '-',
            path: '/acupuncture/selectTreatment',
            component: zj_select_treatment
          },
          {
            name: '-',
            path: '/acupuncture/discriminate',
            component: zj_discriminate
          },
          {
            name: '-',
            path: '/acupuncture/symptom',
            component: zj_symptom
          },
          {
            name: '-',
            path: '/acupuncture/case_info',
            component: case_info
          },
        ]
      },
      {
        name: '',
        path: '/database',
        component: database,
        redirect: '/database/dict_xiyao',
        children: [
          {
            name: '-',
            path: '/database/dict_xiyao',
            meta:{authCode: '5001'},
            component: db_xiyao
          },
          {
            name: '-',
            path: '/database/dict_zhongyao',
            meta:{authCode: '5001'},
            component: db_zhongyao
          },
          {
            name: '-',
            path: '/database/dict_danwei',
            meta:{authCode: '5001'},
            component: db_danwei
          },
          {
            name: '-',
            path: '/database/dict_muban',
            meta:{authCode: '5001'},
            component: db_muban
          },
          {
            name: '-',
            path: '/database/dict_xiangmu',
            meta:{authCode: '5001'},
            component: db_xiangmu
          },
          {
            name: '-',
            path: '/database/dict_huizong',
            meta:{authCode: '5001'},
            component: db_huizong
          },
          {
            name: '-',
            path: '/database/dict_mingxi',
            meta:{authCode: '5001'},
            component: db_mingxi
          },
          {
            name: '-',
            path: '/database/dict_kucun',
            meta:{authCode: '5001'},
            component: db_kucun
          },
        ]
      },
      {
        name: '',
        path: '/reports',
        component: database,
        children: [
          {
            name: '-',
            path: '/reports/jiuzhen',
            component: bb_jiuzhen
          },
          {
            name: '-',
            path: '/reports/ysgz',
            component: bb_ysgz
          },
          {
            name: '-',
            path: '/reports/ksgz',
            component: bb_ksgz
          },
        ]
      },
      {
        name: '',
        path: '/finance',
        meta:{authCode: '6001'},
        component: finance,
      },
      {
        name: '',
        path: '/drug',
        meta:{authCode: '6001'},
        component: drug,
      },
      {
        name: '',
        path: '/auth',
        meta:{authCode: '6001'},
        component: auth,
      },
      {
        name: '',
        path: '/anorectal',
        component: anorectal,
        redirect: '/anorectal/list',
        children: [
          {
            name: '-',
            path: '/anorectal/list',
            component: patient_list
          },
          {
            name: '-',
            path: '/anorectal/treatment',
            component: treatment
          },
          {
            name: '-',
            path: '/anorectal/immediate',
            component: immediate
          },
          {
            name: '-',
            path: '/anorectal/selectSymptom',
            component: select_symptom
          },
          {
            name: '-',
            path: '/anorectal/selectTreatment',
            component: select_treatment
          },
          {
            name: '-',
            path: '/anorectal/discriminate',
            component: discriminate
          },
          {
            name: '-',
            path: '/anorectal/symptom',
            component: symptom
          },
          {
            name: '-',
            path: '/anorectal/case_info',
            component: case_info
          },
        ]
      }
    ]
  },
  {
    name: '',
    path: '/printPage',
    component: printPage
  },
  {
    name: '',
    path: '/print',
    component: () => import ('../view/printPage/print.vue'),
    children: [
      {
        name: '',
        path: 'check',
       component: () => import ('../view/printPage/check.vue')
      },
      {
        name: '',
        path: 'linzheng',
       component: () => import ('../view/printPage/linzheng.vue')
      },
      {
        name: '',
        path: 'western',
       component: () => import ('../view/printPage/westernMedicine.vue')
      }
    ]
  },
  {
    name: '',
    path: '/printPrescription',
    component: printPrescription //user_load
  },
  {
    name: '',
    path: '/userLoad',
    component: user_load
  },
  {
    name: '',
    path: '/login',
    component: login
  },
  {
    name: '',
    path: '/sign',
    component: sign
  },
  {
    name: '404',
    path: '*',
    component: not_find
  }
]

export default list



