<template>
    <el-row type="flex">
      <el-col :xs="8" :sm="6" :md="5" :lg="4" :xl="3">
        <div class="header-logo">
          <img src="../../../static/img/logo.png" alt="">
        </div>
      </el-col>
      <el-col>
        <div class="header-bar-mid">
        : {{keshi}}
          <span v-if="patientIsSave">
            <span>: {{patientName}}</span>
            <span>: {{patientSex}}</span>
            <span>: {{patientAge}}</span>
            <span>: {{patientIsYuFu}}</span>
            <span>: {{patientWeight}}</span>
            <span><el-button plain size="mini" icon="el-icon-edit" @click="handelOpenInfo"></el-button></span>
          </span>
        </div>
      </el-col>
      <el-col :xs="4" :sm="6" :md="5" :lg="4" :xl="3">
        <div class="header-bar-right">
          <span v-if="userName === 'ning'" @click="handleReload">  </span>
          <img v-if="userSex =='1'" src="../..//assets/imgs/admin_head1.png" alt="">
          <img v-if="userSex =='0'" src="../..//assets/imgs/admin_head2.png" alt="">
          <el-dropdown style="margin-left: 16px" @command="handleCommand">
            <span class="el-dropdown-link" style="cursor: pointer;">
              {{userName}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="info"></el-dropdown-item>
              <el-dropdown-item command="passward"></el-dropdown-item>
              <el-dropdown-item command="logout"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        </el-col>
          <el-dialog
            :visible.sync="infoVisible"
            title=""
            align="center"
            width="70%">
            <patient-info style="padding: 0 12px;" ref="child" @childClose="handelCloseDialogInfo" :uid="patientRegistrationNum"></patient-info>
          </el-dialog>
          <el-dialog @close="dialogClosePwd"
            :visible.sync="dialogPassword"
            width="50%">
            <el-row>
              <el-col :span="23">
                <el-form :rules="rules" ref="formPwd" :model="passwordData" label-width="120px">
                    <el-form-item prop="oldpwd" label="">
                      <el-input @keydown.enter.native="funChangePassword"
                      v-model="passwordData.oldpwd" placeholder="" type="password" ></el-input>
                    </el-form-item>
                    <el-form-item prop="password1" label="">
                      <el-input @keydown.enter.native="funChangePassword"
                      v-model="passwordData.password1" placeholder="" type="password" ></el-input>
                    </el-form-item>
                    <el-form-item prop="password2" label="">
                      <el-input @keydown.enter.native="funChangePassword"
                      v-model="passwordData.password2" placeholder="" type="password" ></el-input>
                    </el-form-item>
                </el-form>
              </el-col>
              <el-col :span="24" class="toolbar" align=center>
                <el-button @click="dialogPassword = false"> </el-button>
                <el-button type="primary" @click="funChangePassword"> </el-button>
              </el-col>
            </el-row>
          </el-dialog>
          <el-dialog
            title="" center @close="dialogCloseUser"
            :visible.sync="dialogUser"
            width="50%">
            <el-row>
              <el-col :span="23">
                <el-form :rules="rules" ref="formUser" :model="userData" label-width="120px">
                    <el-form-item label=":" prop="username">
                      <el-input v-model="userData.username" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label=":" prop="sex">
                      <el-select
                          v-model="userData.sex" placeholder="" style="width:100%">
                            <el-option
                              v-for="item in selectSex"
                              :key="item.value"
                              :label="item.lable"
                              :value="item.value">
                            </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label=":">
                      <el-select multiple
                      v-model="deptSelectDisplay" disabled  style="width:100%">
                        <el-option
                          v-for="item in keshiData"
                          :key="item.id"
                          :label="item.deptName"
                          :value="item.deptCode">
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label=":">
                      <el-input v-model="userData.mobile" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label=":">
                      <el-input v-model="userData.address" placeholder=""></el-input>
                    </el-form-item>
                </el-form>
              </el-col>
              <el-col :span="24" class="toolbar" align=center>
                <el-button @click="dialogUser = false"> </el-button>
                <el-button type="primary" @click="funChangeUser"> </el-button>
              </el-col>
            </el-row>
          </el-dialog>
    </el-row>
</template>
<script>
import {
  logout,  // 
  getUserById, // 
  editPwdByAccount, // 
  updateUser, // 
} from '../../api'
import patient_info from '../../components/patient_info_window'
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {
  components:{
    'patient-info': patient_info
  },
    data(){
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.passwordData.password1) {
          callback(new Error('!'));
        } else {
          callback();
        }
      }
      return{
        photoImg: false, // 
        infoVisible: false, // 
        dialogPassword: false, // 
        dialogUser: false, // 
        cuid: '', // 
        value:'',
        userName: '',
        userSex: '',
        mag: '',
        passwordData:{ // 
          oldpwd: '',
          password1: '',
          password2: ''
        },
        userData:{ // 

        },
        selectSex: [
        {
          lable: '',
          value: '1',
        },
        {
          lable: '',
          value: '0',
        },
      ],
        deptSelectDisplay: [], // 
        deptSelectData: '', // 
        rules:{ // validator: validatePass2
          username: [{required: true, message: '!', trigger: 'blur'}],
          oldpwd: [{required: true, message: '!', trigger: 'blur'}],
          password2: [
            {required: true, message: '!', trigger: 'blur'},
            {validator: validatePass2, trigger: 'blur'}
            ],
          password1: [
            {required: true, message: '!', trigger: 'blur'},
            {min: 4, max: 9, message: '49!', trigger: 'blur'},]
      },
      //  userName: '', // 
      }
    },
    computed:{
      ...mapState('patient',{
        patientName: state => state.name,
        patientSex: state => state.sex,
        patientAge: state => state.age,
        patientIsYuFu: state => state.isYuFu,
        patientRegistrationNum: state => state.registrationNum,
        patientWeight: state => state.weight,
        patientIsSave: state => state.isSave,
      }),
      ...mapState('common',{
        keshiData:state=>state.keshi,
      }),
      keshi:function(){
        let ks=''
        let path = this.$route.path
        if(path.includes('register'))
          ks=""
        if(path.includes('clinical'))
          ks=""
        if(path.includes('acupuncture'))
          ks=""
        if(path.includes('anorectal'))
          ks=""
        if(path.includes('auth'))
          ks=""
        if(path.includes('finance'))
          ks=""
        if(path.includes('drug'))
          ks=""
        if(path.includes('register'))
          ks=""
        return ks
      },
      patient:function(){ // 
        return this.$store.state.patient
      },
      registrationNum:function(){
        return this.$store.state.registrationNum
      }
    },
    methods:{
      ...mapMutations('user',['saveUserDataByInfo','removeUserData']),
      handelOpenInfo(){
        this.infoVisible = true
        this.$nextTick(()=>{
          this.$refs['child'].getData()
        })
      },
      handleReload () {
        window.location.reload()
      },
      handleCommand(command){ // 
        if(command == 'logout'){
          logout()// 
          this.removeUserData()
          this.$router.push('login')
        }
        if(command == 'passward'){
          this.passwordData.oldpwd = ''
          this.passwordData.password1 = ''
          this.passwordData.password2 = ''
          this.dialogPassword = true
        }
        if(command == 'info'){ //  - 
          getUserById(sessionStorage.getItem('uuid')).then(res=>{
            if(res.data.code == '0'){
              let user = res.data.data.user
              this.userData = user
              sessionStorage.setItem('sex', user.sex)
              sessionStorage.setItem('username', user.username)
              this.deptSelectData = user.deptcode
              this.deptSelectDisplay = user.deptcode.toString().split(',')
              this.userName = user.username
              this.userSex = user.sex
            }
          })
          this.dialogUser = true
        }
      },
      dialogCloseUser(){ // 
        this.$refs.formUser.resetFields()
      },
      dialogClosePwd(){ // 
        this.$refs.formPwd.resetFields()
      },
      funChangeUser(){ // 
        if(!this.userData.uuid){
          return
        }
        this.$refs.formUser.validate((valid) => {
          if (valid) {
            let userData=this.userData
            userData.deptcode = this.deptSelectData
            updateUser(userData).then(res=>{
              if(res.data.code == '0'){
                this.$message.success('!')
                this.userName = userData.username
                this.userSex = userData.sex
                sessionStorage.setItem('sex', userData.sex)
                sessionStorage.setItem('username', userData.username)
              }else{
                this.$message.error('!')
              }
            })
          } else {
            return false
          }
        })
      },
      // 
      funChangePassword(){
        this.$refs.formPwd.validate((valid) => {
          if(valid){
              editPwdByAccount({
                oldpwd: this.passwordData.oldpwd,
                usercode: sessionStorage.getItem('usercode'),
                password: this.passwordData.password1,
              }).then(res=>{
                if(res.data.code=='0'){
                  this.$message.success('!')
                  this.dialogPassword = false
                }else{
                  this.$message.error(res.data.msg)
                }
              })
          }
        })

      },
      handelCloseDialogInfo(){
        this.infoVisible = false
      }
    },
    mounted:function(){
      this.userName = sessionStorage.getItem('username')
      this.userSex = sessionStorage.getItem('sex')
    }
}
</script>
<style scoped>
.header-logo{
  position: relative;
  height: 60px;
  /* border-right: 1px solid #aaa; */
  line-height: 60px;
  max-width: 206px;
}
.header-logo img{
  height: 39px;
  vertical-align: middle;
}
.header-bar-mid{
  padding-left: 20px;
  height: 60px;
  line-height: 80px;
  vertical-align: bottom;
}
.header-bar-mid span span{
  margin-left:24px;
}
.header-bar-right{
  padding-right: 14px;
  height: 60px;
  width: 100%;
  text-align: right;
  line-height: 60px;
}
.header-bar-right img{
  vertical-align: middle;
  max-width: 40px;
}

</style>




