// 
// this.$store.state.xxx.xxxx
import {
  getDrugWayTable, // 
  getUnitList, // 
  getDrugWay, // 
  getDosage, // 
} from '../../api'
const state = {
  unitAllList: [], // 
  drugWayList: [], // 
  dosageList: [], // 
  unitnameList: [], // 
  prescriptionunitnameList: [], // 
  singlemeasureunitnameList: [], // 
};
const getters = {

};
const actions = {
};
const mutations = {
  getDatabaseCommon:(state)=>{
    getUnitList({pageSize: 99999}).then(res=>{
      if(res.data.code=='0'){
        state.unitAllList = res.data.data.content
      }
    })
    getDrugWay().then(res=>{
      if(res.data.code=='0'){
        state.drugWayList = res.data.data
      }
    })
    getDosage().then(res=>{
      if(res.data.code=='0'){
        state.dosageList = res.data.data
      }
    })
  }
};
export default {
  namespaced: true,  //
  state,
  mutations,
  actions,
  getters
};  


