<template>
  <el-row>
    <el-form label-width="100px" :model="formData" :rules="rules">
      <el-col :offset="0" :span="11">
        <el-form-item label="">
          <el-input v-model="formData.registrationNum" readonly disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="deptCode">
          <el-select v-model="formData.deptCode" @change="selectDept" placeholder="">
              <el-option
                v-for="item in optionsDeptName"
                :key="item.id"
                :label="item.deptName"
                :value="item.deptCode">
              </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="onSetDate">
            <el-date-picker
            v-model="formData.onSetDate"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="">
          </el-date-picker>
      </el-form-item>
      <el-form-item label="">
          <el-select v-model="formData.emSex" placeholder="">
            <el-option
              v-for="item in optionsSex"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.emComName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="emType">
          <el-select v-model="formData.emType" placeholder="" size="medium">
            <el-option
              v-for="item in costType"
              :key="item.value"
              :label="item.mecidalTypeName"
              :value="item.mecidalTypeCode">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="">
          <el-select v-model="formData.isYuFu" placeholder="">
            <el-option
              v-for="item in optionsYuFu"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.weight"></el-input>
        <span class="input-fix">kg</span>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.maker"></el-input>
      </el-form-item>
      </el-col>
      <el-col :offset="1" :span="11">
      <el-form-item label="" prop="mecidalNum">
        <el-input v-model="formData.mecidalNum"></el-input>
      </el-form-item>
      <el-form-item label="" prop="userName">
        <el-autocomplete v-model="formData.userName"
          :fetch-suggestions="queryUserList"
          @select="handleSelect"
          style="width: 100%"
          placeholder=""
        ></el-autocomplete>
      </el-form-item>
      <el-form-item label="" prop="emName">
        <el-input v-model="formData.emName"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.emAge"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.emMobile"></el-input>
      </el-form-item>
      <el-form-item label="" prop="mecidalType">
          <el-select v-model="formData.mecidalType" placeholder="" size="medium">
            <el-option
              v-for="item in mecidalType"
              :key="item.value"
              :label="item.mecidalTypeName"
              :value="item.mecidalTypeCode">
            </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.emAddress"></el-input>
      </el-form-item>
      <el-form-item label="" prop="emNum">
        <el-input v-model="formData.emNum"></el-input>
      </el-form-item>
      <el-form-item label="">
          <el-date-picker
          v-model="formData.makeDate"
          type="datetime"
          value-format="yyyy-MM-dd HH:mm:ss"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      </el-col>
    </el-form>
    <el-col :span="24" align="center" class="toolbar">
      <el-button type="primary" style="margin-right: 90px" @click="update"> </el-button>
      <el-button type="primary" @click="handelClose"> </el-button>
    </el-col>
  </el-row>
</template>
<script>
import { mapState,mapMutations} from 'vuex'
import {
  getSelectPatientInfoTable, // 
  updatePatientInfo, // 
  getGyUser, // 
  } from "../api"
export default {
 props:['uid'],
    computed:{
    ...mapState({
      optionsDeptName: state => state.common.keshi, // 
      mecidalType: state=> state.common.mecidalType, // 
      costType: state=> state.common.costType,
    }),
  },
  data(){
    return{
      formData:{
        emType: '',
      }, // 
      // uid: function () {
      //   return sessionStorage.getItem('current_registrationNum')
      // },
      optionsYuFu:[ // 
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        }
      ],
       optionsSex:[{
          value: 1,
          label: ''
       },
       {
          value: 0,
          label: ''
       }],
      optionsMecidal: [], //   
      rules:{ // 
        emName:[
          {required: true, message: '', trigger: 'blur'}
        ],
      },
      rules:{ // 
        emName:[{required: true, message: '', trigger: 'blur'}],
        mecidalNum:[{required: true, message: '', trigger: 'blur'}],
        deptCode:[{required: true, message: '', trigger: 'change'}],
        userName:[{required: true, message: '', trigger: 'change'}],
        mecidalType:[{required: true, message: '', trigger: 'change'}],
        emNum: [{required: true, message: '', trigger: 'blur'}],
        emType: [{required: true, message: '', trigger: 'change'}],
        onSetDate: [{required: true, message: '', trigger: 'change'}],
      },
    }
  },
  methods:{
    ...mapMutations('patient',['getPatientInfo']),
    handelClose(){ // 
      this.$emit('childClose')
    },
    update(){
      updatePatientInfo(this.formData).then(res=>{
        if(res.data.code=='1'){
          this.$message.success('')
          // store 
          this.getPatientInfo({id:this.formData.registrationNum})
          this.getData()
          this.handelClose()
        }else{
          this.$message.error('')
        }
      })
    },
      queryUserList(queryString,cb){ // 
      if(this.formData.deptCode == ''){
          this.$message.error('')
          cb([])
      }else{
        getGyUser({deptCode: this.formData.deptCode}).then(res=>{
          let arr=[]
          if(res.data.code == '1'){
            this.$message.error('')
            cb([])
          }else{
            res.data.data.forEach(element=>{
              arr.push({value: element.userName,id: element.userCode})
            })
            cb(arr)
          }
        })
      }
    },
    handleSelect(item){ // 
      this.formData.userCode = item.id
    },
    selectDept(item){
      let DeptName = ''
      this.optionsDeptName.forEach(element => {
        if(element.id==item){
          DeptName = element.deptName
        }
      })
      this.formData.deptName = DeptName
    },
    getData(){
      getSelectPatientInfoTable({registrationNum: this.uid}).then(res=>{
        this.formData = res.data.data
        this.formData.emType = this.formData.emType.toString()
        this.formData = {...this.formData}
      })
    }
  },
  mounted(){
    // getData
    // getSelectPatientInfoTable({registrationNum: this.uid}).then(res=>{
    //   this.formData = res.data.data
    //   this.formData.emType = this.formData.emType.toString()
    // })
  }
}
</script>

<style scoped>
.huanzhe{
  padding-top: 24px;
}
.el-select,.el-date-editor{
  width: 100%;
}
input{
  min-width: 480px !important;
}
.huanzhe-btn{
  margin-top: 24px;
}
.huanzhe-btn button{
  display: block;
  margin: 0 auto;
}
</style>


